:root {
  --nav-font-color: rgb(255, 255, 255);
  --nav-font-color-hover: rgb(159, 159, 159);
  --logo-font: "Kanit", sans-serif;
  --page-back-color: #282c34;
}

.App {
  text-align: center;
}

.disabled {
  display: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.page-container {
  background-color: var(--page-back-color);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  max-width: 90%;
  margin: 0 auto;;
}

