.Post {
    background-color: rgba(0, 255, 255, 0.178);
    display: flex;
    flex-direction: column;
    padding: 20px;
    justify-content: space-between;
}

.Post * {
    text-align: left;
}

.post__image {
    width: 25%;
    height: fit-content;
}

.post__description {
    display: block;
    width: 65%;
}

.post__main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.post__dates {
    margin-top: 20px;
    color: rgba(0, 0, 0, 0.4);
    font-weight: 100;
    font-size: 0.85em;
}

.Post p {
    font-size: 1.1rem;
    font-weight: 400;
}

.Post h2 {
    font-size: 2rem;
    font-weight: 400;
}