header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #333;
  color: var(--nav-font-color);
  padding: 20px 0;
  /* z-index: 100; */
  /* position: relative; */
}

.Navbar__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 80%;
  margin: 0 auto;
}

.logo {

  .link {
    display: flex;
    flex-direction: row;
    gap: 15px;
    text-align: start;
    align-items: center;
    width: 70px;
    height: auto;
    margin-right: 40px;
    color: var(--nav-font-color);
    text-decoration: none;
  }

  img {
    height: 100%;
    width: 100%;
  }

  .stick {
    width: 10px;
    height: 100%;
    background-color: var(--nav-font-color);
    border: 5px;
  }
}

nav {

  ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      margin-right: 20px;

      .link {
        color: var(--nav-font-color);
        text-decoration: none;
        font-size: 18px;
        font-weight: bold;
      }

      a:hover {
        color: var(--nav-font-color-hover);
      }
    }
  }
}

.search {
  display: flex;
  align-items: center;
  background-color: #444;
  border-radius: 5px;
  padding: 5px;

  input {
    margin-right: 5px;
    border: none;
    background-color: transparent;
    color: var(--nav-font-color);
    font-size: 16px;
    padding: 5px;
  }

  button {
    background-color: #555;
    border: none;
    color: var(--nav-font-color);
    font-size: 16px;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }

  button:hover {
    background-color: #666;
  }

  input:focus-visible {
    outline: 1px ridge var(--nav-font-color-hover);
    border-radius: 2px;
  }
}

.logo__title {
  font-family: var(--logo-font);
  font-weight: 800;
  font-style: normal;
  font-size: 24px;
  /* font-weight: bold; */
}

.menu-icon {
  display: none;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .Navbar__container > nav {
    display: none;
  }

  .menu-icon {
    display: block;
  }

  .alter-nav {
    padding: 20px;
    background-color: #444;
    position: absolute;
    right: 0;
    top: 0;
    height: 100vh;
  }
}